/* AlpineJS Helpers */
[x-cloak] {
  display: none !important;
}

/* THEME COMPONENTS
------------------------------------------------------------------------------*/
.component-accordion .accordion-item.border-b-4 {
  border-bottom-width: 4px !important;
}
.component-accordion .accordion-item {
  cursor: default;
}
.component-accordion .accordion-item ul, .component-accordion .accordion-item ol {
  padding-left: 1rem;
}
.component-accordion .accordion-item ul {
  list-style: disc;
}
.component-accordion .accordion-item ol {
  list-style: decimal;
}
.component-accordion .accordion-item:hover h2, .component-accordion .accordion-item:hover h3, .component-accordion .accordion-item:hover h4, .component-accordion .accordion-item:hover h5, .component-accordion .accordion-item:hover h6 {
  text-decoration: underline;
}

.wicket-breadcrumb .icon {
  display: block;
  margin: 0 10px;
}
.wicket-breadcrumb span.bold {
  font-weight: bold;
}
.wicket-breadcrumb.reversed {
  color: #fff;
}
.wicket-breadcrumb.reversed .icon {
  color: #fff;
}

/* Base Button */
.button {
  background-color: transparent;
  border: 2px solid var(--wp--preset--color--primary);
  padding: 14px 16px;
  font-size: var(--wp--preset--font-size--body-md, 16px);
  cursor: pointer;
  text-decoration: none;
  border-radius: var(--wp--custom--border-radius--050);
  gap: 6px;
  font-weight: bold;
  line-height: 100%;
  /* Rounded Button */
  /* Disabled */
}
.button.border-0 {
  border: 0;
}
.button.button--rounded {
  border-radius: 100px;
  width: 48px;
  justify-content: center;
}
.button:disabled {
  pointer-events: none;
}

/* -------------------------------------------------------------------------
 * BUTTON SIZES
 * ------------------------------------------------------------------------- */
/* Small Button */
.button--sm {
  padding: 11px 12px;
  font-size: var(--wp--preset--font-size--body-sm, 14px);
  /* Rounded Button */
}
.button--sm.button--rounded {
  width: 40px;
}

/* Large Button */
.button--lg {
  padding: 20px;
